// Texta styles.
// See @mixin ma-texta-style in 00-base/mixins/_ma-typography.scss

$styles: (
  "light": 'Light',
  "book": 'Book',
  "medium": 'Medium',
  "bold": 'Bold',
  "black": 'Black'
);

.ma-texta {
  @each $class, $s in $styles {
    &-#{$class} {
      @include ma-texta-style($s)
      &--italic {
        @include ma-texta-style($s, true)
      }
    }
  }
}
